import Vue from 'vue'
import Vuex from "vuex";
Vue.use(Vuex);
var store = new Vuex.Store({
	state:{
		shop:true
	},
	mutations:{
		showState:function(state){
			state.shop=true;
		},
		hideState:function(state){
			state.shop=false;
		},
	}
})
export default store;


