<template>
	<div id="app">
		<ul @click="showHide">
			<li v-for="item in list">
				姓名：{{item.name}} 年龄：{{item.age}}
			</li>
		</ul>
		<input type="button" name="" id="" value="显示" />
		<router-link to="/">默认首页</router-link>
		<router-link to="/main">main</router-link>
		<router-link to="/page_one">page_one</router-link>
		<router-link to="/page_two">page_two</router-link>
		<div>
			<router-view/>
		</div>

	</div>
</template>

<script>
	import dataList from "./dataList/dataList.js"
	export default {
		data: function() {
			return {
				list: dataList
			}
		},
		computed:{
			showHide:function(){
				return false;
			}
		}
	}
</script>

<style>

</style>


